code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0; }

ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

* {
  box-sizing: border-box;
  text-decoration: none; }

html {
  color: #141414;
  font-family: sans_pro, sans-serif, Arial, serif;
  font-size: 16px; }

body {
  margin: auto;
  font-size: 1rem;
  height: 100%;
  background-color: #f8f8f1;
  /* Disables pull-to-refresh but allows overscroll glow effects. */
  overscroll-behavior-y: contain; }

.App {
  text-align: center;
  max-width: 100%; }

.route {
  height: 100vh; }
  .route h1 {
    height: 2.5rem; }

@font-face {
  font-family: sans_pro;
  src: url(/device-manager/static/media/SourceSansPro-Regular.3f482321.ttf); }

@font-face {
  font-family: sans_pro_light;
  src: url(/device-manager/static/media/SourceSansPro-Light.3ba9836f.ttf); }

@font-face {
  font-family: sans_pro_bold;
  src: url(/device-manager/static/media/SourceSansPro-Bold.7e0ee8c4.ttf); }

.manage-device {
  height: calc(100vh - 2.5rem);
  display: flex;
  flex-direction: column; }
  .manage-device .bar {
    position: relative;
    min-height: 2rem; }
    .manage-device .bar h2 {
      height: 2rem; }
    .manage-device .bar .only-mobile .mask-layer {
      z-index: 1000000000;
      position: fixed;
      height: 100%;
      width: 100%;
      background-color: rgba(95, 70, 45, 0.05);
      top: 0;
      left: 0; }
    .manage-device .bar .only-mobile button {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%; }
  .manage-device .content {
    border-top: 1px solid #969696;
    height: 100%; }

/********** Cmps **********/
.manage-device-list {
  flex-grow: 1;
  max-width: 220px;
  min-width: 140px;
  overflow: auto;
  border-right: 1px solid #969696;
  z-index: 1000000001; }
  .manage-device-list .item {
    background-color: #ddebf1;
    cursor: pointer;
    padding: 12px 0;
    border-bottom: 1px solid #969696; }
    .manage-device-list .item:hover {
      background-color: #c3e7f8; }
    .manage-device-list .item.selected {
      background-color: #a4d2e7; }
  .manage-device-list.mobile {
    position: absolute;
    top: 100%;
    left: 0;
    border: 1px solid #969696;
    box-shadow: 0px 0px 2px 0px #969696;
    min-width: 60vw;
    width: 250px;
    max-width: 75vw;
    max-height: calc(100vh - 9rem);
    border-radius: 2px; }

.manage-device-preview {
  height: 100%;
  background-color: #cdc9e8;
  flex-grow: 1;
  display: flex;
  flex-direction: column; }
  .manage-device-preview .categories .category {
    cursor: pointer;
    background-color: blanchedalmond;
    border-bottom: 1px solid #969696;
    padding: 8px 0;
    width: 33.333333%;
    min-width: 130px;
    flex-grow: 1; }
    .manage-device-preview .categories .category.selected {
      background-color: #ffd89e;
      font-weight: bold; }
    .manage-device-preview .categories .category:hover {
      background-color: #ffe6c2; }

.manage-device-preview .visual-display {
  overflow: auto;
  height: 100%;
  position: relative; }
  .manage-device-preview .visual-display .device {
    position: absolute; }
    .manage-device-preview .visual-display .device .icon {
      height: 100px;
      border-radius: 4px;
      -webkit-transition: background-color 0.3s;
      transition: background-color 0.3s; }
      .manage-device-preview .visual-display .device .icon img {
        height: 100%; }
    .manage-device-preview .visual-display .device .title {
      width: 100%;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      padding: 4px 0 0; }
      .manage-device-preview .visual-display .device .title span {
        background-color: rgba(255, 224, 207, 0.75);
        border-radius: 4px;
        padding: 0 4px; }
    .manage-device-preview .visual-display .device .device-btn-options {
      background-color: transparent;
      outline: transparent;
      padding: 0;
      border: none;
      height: 38px;
      position: absolute;
      top: -27px;
      left: 31px;
      opacity: 0.8; }
      .manage-device-preview .visual-display .device .device-btn-options img {
        height: 100%;
        background-color: #c8edff;
        border-radius: 50%; }
    .manage-device-preview .visual-display .device .options {
      width: 140%;
      position: absolute;
      left: -20%;
      top: 14px;
      display: flex;
      justify-content: space-between;
      background-color: rgba(216, 192, 192, 0.705); }
      .manage-device-preview .visual-display .device .options img {
        height: 38px;
        background-color: #ffdae1; }
  .manage-device-preview .visual-display .svg-btn-options {
    position: absolute;
    z-index: 5;
    opacity: 0.8;
    border-radius: 50%; }
    .manage-device-preview .visual-display .svg-btn-options .btn {
      height: 100%;
      border-radius: 50%;
      background-color: #ffeccebf; }
    .manage-device-preview .visual-display .svg-btn-options .options {
      height: 100%;
      border-radius: 50%;
      position: absolute;
      top: calc(100% + 4px);
      left: 0;
      opacity: 0.9;
      background-color: #ffcecebf; }
  .manage-device-preview .visual-display svg {
    position: absolute; }
    .manage-device-preview .visual-display svg line {
      stroke: #a4fff5;
      stroke-linecap: square;
      stroke-dasharray: 0 10;
      stroke-miterlimit: 5;
      stroke-linejoin: round;
      stroke-width: 6px; }

.manage-device-preview .device-details h2 {
  margin: 0.5rem 0; }

.manage-device-preview .device-details .list {
  text-align: left;
  padding: 0 16px;
  display: grid;
  grid-gap: 6px; }
  .manage-device-preview .device-details .list p {
    padding: 4px;
    background-color: rgba(207, 249, 255, 0.3);
    display: grid;
    grid-template-columns: 130px 1fr; }
    .manage-device-preview .device-details .list p:last-child:nth-child(odd) {
      grid-column: 1/3; }
    .manage-device-preview .device-details .list p .title {
      font-weight: bold; }

.manage-device-preview .device-history h2 {
  margin: 0.5rem 0; }

/************************** Tablet and Desktop **************************/
@media (min-width: 64px) and (max-width: 279px) {
  html {
    font-size: 10px; } }

@media (min-width: 280px) and (max-width: 479px) {
  html {
    font-size: 14px; } }

@media (min-width: 480px) and (max-width: 767px) {
  html {
    font-size: 16px; } }

@media (min-width: 768px) {
  .manage-device-preview .device-details .list {
    grid-template-columns: 1fr 1fr; } }

@media (min-width: 1024px) and (max-width: 1699px) {
  html {
    font-size: 18px; } }

@media (min-width: 1700px) and (max-width: 1919px) {
  html {
    font-size: 20px; } }

@media (min-width: 1920px) and (max-width: 2399px) {
  html {
    font-size: 22px; } }

@media (min-width: 2400px) {
  html {
    font-size: 24px; } }

/**************** Helpers ****************/
/********** Flex **********/
.flex {
  display: flex; }

.space-between {
  justify-content: space-between; }

.space-even {
  justify-content: space-evenly; }

.space-around {
  justify-content: space-around; }

.space-center {
  justify-content: center; }

.align-center {
  align-items: center; }

.flex.wrap {
  flex-wrap: wrap; }

.flex-col {
  display: flex;
  flex-direction: column; }

/********** Lists **********/
.clean-list {
  list-style-type: none;
  margin: 0;
  padding: 0; }

.align-left {
  text-align: left; }

.text-wrap {
  overflow-wrap: break-word;
  word-wrap: break-word;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto; }

